import React, { useState } from "react";
import moment from "moment";
import { TextField } from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";

function getWeeksOptions(weeksCount) {
    return Array.from({ length: weeksCount }).map((_, index) =>
        String(index + 1)
    );
}

export default function ({ setWeek, selectedWeek }) {
    const currentWeek = moment().week();

    const [weeksOptions] = useState(getWeeksOptions(currentWeek));

    const changeWeek = (e, value) => {
        setWeek(value);
    };

    const value = selectedWeek && String(selectedWeek);

    return (
        <Autocomplete
            value={value}
            options={weeksOptions}
            onChange={changeWeek}
            renderInput={params => (
                <TextField {...params} label="Week number" />
            )}
        />
    );
}
