// Core
import React from "react";
import moment from "moment";
import { useFormik } from "formik";

// UI
import { TextField, Typography, Grid } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import TimePicker from "components/TimePicker";
import FormDialog from "components/FormDialog";
import Selector from "../Selector";

// Styles
import useStyles from "./styles";

// Utils
import { getInitialFormValues } from "utils/developerReportUtils";

// Constants
import { validationErrorMessages } from "constants/developerReport";
import {
    FULL_DATE_FORMAT,
    DATE_FORMAT,
    TIME_FORMAT,
} from "constants/dateFormats";

function validateTimeRange(from, to) {
    return moment(from, TIME_FORMAT).isBefore(moment(to, TIME_FORMAT));
}

function validate(values) {
    const errors = {};
    if (!validateTimeRange(values.from_time, values.to_time)) {
        errors.from_time = validationErrorMessages.timeInterval;
        errors.to_time = validationErrorMessages.timeInterval;
    }
    return errors;
}

export default function LogForm({
    open,
    handleClose,
    projects,
    logTypes,
    submitForm,
    initialData,
    deleteLog,
    selectedDay,
}) {
    const classes = useStyles();

    const initialValues = getInitialFormValues(initialData);

    if (!initialData) {
        initialValues.date = moment(selectedDay, FULL_DATE_FORMAT).format(
            FULL_DATE_FORMAT
        );
    }

    const formik = useFormik({
        initialValues,
        enableReinitialize: true,
        validate,
        onSubmit: async data => {
            await submitForm(getInitialFormValues(data), initialData?.id);
            handleClose();
        },
    });

    const handleDeleteLog = async () => {
        await deleteLog(initialData.id);
        handleClose();
    };

    const workingDay = moment(formik.values.date, FULL_DATE_FORMAT).format(
        DATE_FORMAT
    );

    return (
        <FormDialog open={open} handleClose={handleClose} title="Work Item:">
            <Typography variant="subtitle1" gutterBottom>
                {`Working day: ${workingDay}`}
            </Typography>
            <form autoComplete="off">
                <Grid container className={classes.root}>
                    <Grid item xs={12} sm={5}>
                        <Selector
                            placeholder="Project name"
                            data={projects}
                            labelKey="name"
                            valueKey="id"
                            onChange={formik.setFieldValue}
                            id="project_id"
                            value={formik.values["project_id"]}
                        />
                    </Grid>
                    <Grid item xs={12} sm={7}>
                        <Grid container wrap="nowrap">
                            <TimePicker
                                id="from_time"
                                label="Start time"
                                error={formik.errors.from_time}
                                value={formik.values["from_time"]}
                                onChange={formik.setFieldValue}
                            />
                            <TimePicker
                                margin="normal"
                                id="to_time"
                                error={formik.errors.to_time}
                                label="End time"
                                value={formik.values["to_time"]}
                                onChange={formik.setFieldValue}
                            />
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="descriptions"
                            fullWidth
                            defaultValue={formik.values.descriptions}
                            onChange={formik.handleChange}
                            label="Description"
                            multiline
                            rows={4}
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item md={4} xs={12}>
                        <Selector
                            data={logTypes}
                            placeholder="Log type"
                            labelKey="type"
                            valueKey="id"
                            onChange={formik.setFieldValue}
                            id="log_type_id"
                            value={formik.values["log_type_id"]}
                        />
                    </Grid>
                </Grid>
                <DialogActions>
                    {initialData && (
                        <Button onClick={handleDeleteLog} color="secondary">
                            Delete
                        </Button>
                    )}
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={formik.handleSubmit} color="primary">
                        Save
                    </Button>
                </DialogActions>
            </form>
        </FormDialog>
    );
}
