// Core
import React from "react";
import moment from "moment";

// UI
import {
    TableContainer,
    TableHead,
    TableCell,
    TableBody,
    Paper,
    Table,
} from "@material-ui/core";
import TableRow from "components/TableRow";

// Constants
import { TABLE_HEADERS } from "constants/developerReport";
import {
    HOUR_IN_MINUTES,
    DATE_FORMAT,
    FULL_DATE_SHORT,
    FULL_DATE_FORMAT,
} from "constants/dateFormats";

function getTimeLabel(time) {
    let result = `${time} hours`;
    const durationMin = moment.duration(time).asMinutes();
    if (durationMin < HOUR_IN_MINUTES) {
        result = `${durationMin} minutes`;
    }
    return result;
}

export default function ({
    data,
    handleEditLog,
    selectedDay,
    selectedWeek,
    projects,
    logTypes,
}) {
    const [filteredLogs, setFilteredLogs] = React.useState([]);

    React.useEffect(() => {
        let logs = data;
        if (selectedDay) {
            logs = data.filter(
                item =>
                    moment(item.date, FULL_DATE_FORMAT).format(DATE_FORMAT) ===
                    moment(selectedDay).format(DATE_FORMAT)
            );
        }
        if (!selectedWeek) {
            logs = [];
        }
        setFilteredLogs(logs);
    }, [data, selectedDay, selectedWeek]);

    const editLog = log => {
        handleEditLog(log);
    };

    const getProjectName = id => projects.find(item => item.id === id)?.name;
    const getLogTypeEnum = id => logTypes.find(item => item.id === id)?.type;

    return (
        <TableContainer component={Paper}>
            <Table aria-label="simple table">
                <TableHead>
                    <TableRow>
                        {TABLE_HEADERS.map(item => (
                            <TableCell key={item}>{item}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {filteredLogs.length ? (
                        filteredLogs.map((log, i) => (
                            <TableRow
                                hover
                                key={log.id}
                                onClick={() => editLog(log)}
                            >
                                <TableCell component="th" scope="row">
                                    {i + 1}
                                </TableCell>
                                <TableCell>
                                    {moment(log.date, FULL_DATE_FORMAT).format(
                                        FULL_DATE_SHORT
                                    )}
                                </TableCell>
                                <TableCell>
                                    {getProjectName(log.project_id)}
                                </TableCell>
                                <TableCell>
                                    {getLogTypeEnum(log.log_type_id)}
                                </TableCell>
                                <TableCell>{getTimeLabel(log.total)}</TableCell>
                                <TableCell>{log.status}</TableCell>
                            </TableRow>
                        ))
                    ) : (
                        <TableRow>
                            <TableCell>{"You don't have any logs"}</TableCell>
                        </TableRow>
                    )}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
