// Core
import React, { useEffect, useState } from "react";
import moment from "moment";

// UI
import { Button, ButtonGroup } from "@material-ui/core";
import {
    DATE_FORMAT,
    DAY_FORMAT,
    FULL_DATE_FORMAT,
} from "constants/dateFormats";

function checkLogDate(logs, date) {
    return logs.some(
        item =>
            moment(item.date, FULL_DATE_FORMAT).format(DATE_FORMAT) ===
            moment(date).format(DATE_FORMAT)
    );
}

export default function ({ selectedWeek, logs, setDay, selectedDay }) {
    const [weekDates, setWeekDates] = useState([]);

    useEffect(() => {
        if (!selectedWeek) {
            setWeekDates([]);
            return;
        }
        const getWeekDates = weekNumber => {
            return Array.from({ length: 7 }).map((_, index) =>
                moment()
                    .week(weekNumber)
                    .weekday(index + 1)
            );
        };

        setWeekDates(getWeekDates(selectedWeek));
    }, [selectedWeek]);

    const formatDate = day => moment(day).format(DAY_FORMAT);
    const isActive = day => day === selectedDay;
    return (
        <ButtonGroup color="primary" variant="text" size="large">
            {weekDates.map(day => {
                const hasLogs = checkLogDate(logs, day);
                return (
                    <Button
                        onClick={() => setDay(day)}
                        size="large"
                        color={
                            isActive(day)
                                ? "secondary"
                                : hasLogs
                                ? "primary"
                                : "default"
                        }
                        variant="contained"
                        key={`date-${day}`}
                    >
                        {formatDate(day)}
                    </Button>
                );
            })}
        </ButtonGroup>
    );
}
