// Core
import get from 'lodash.get'

export function authRequired({ me }) {
    return !!me
}

export function unauthorizedOnly(prop) {
    return !prop.me
}

export function roleAdmin({ me }) {
    return get(me, 'role') === 'admin'
}

export function roleDev({ me }) {
    return get(me, 'role') === 'dev'
}
