// Core
import { observable, action, decorate, runInAction } from "mobx";
import get from "lodash.get";
// Services
import projectService from "../services/api/ProjectService";

const fetchStrategy = ({ body, id }) => ({
    delete: () => projectService.deleteProject(id),
    update: () => projectService.updateProject(body, id),
    create: () => projectService.createProject(body),
});
class ProjectStore {
    projectList = [];
    projectTypes = [];
    projectUserList = [];
    editingProjectData = undefined;

    setInitialState = () => {
        this.projectList = [];
        this.projectTypes = [];
    };

    onGetProjectList = async () => {
        try {
            const data = await projectService.getProjectList();
            this.projectList = get(data, "data.data");
        } catch (err) {
            console.error(err);
        }
    };

    handleFetchProjectData = async (actionType, data) => {
        try {
            await fetchStrategy(data)[actionType]();
            this.onGetProjectList();
        } catch (error) {
            console.error(error);
        }
    };

    onCreateProject = body => {
        this.handleFetchProjectData("create", { body });
    };

    onUpdateProject = (body, id) => {
        this.handleFetchProjectData("update", { body, id });
    };

    onDeleteProject = id => {
        this.handleFetchProjectData("delete", { id });
    };

    onGetProjectTypes = async () => {
        try {
            const data = await projectService.getProjectTypes();
            this.projectTypes = get(data, "data.data");
        } catch (error) {
            console.error(error);
        }
    };

    onGetProjectUserList = async id => {
        try {
            const data = await projectService.getProjectUserList(id);
            return get(data, "data.data");
        } catch (error) {
            console.error(error);
        }
    };

    setEditingProjectData = async project => {
        if (!project) {
            runInAction(() => {
                this.editingProjectData = project;
            });
            return;
        }
        const users = await this.onGetProjectUserList(project.id);
        runInAction(() => {
            this.editingProjectData = {
                ...project,
                users: users.map(item => item.id),
            };
        });
    };

    set projectList(data) {
        this.projectList = data;
    }
    s;
    set projectTypes(data) {
        this.projectTypes = data;
    }
    set projectUserList(data) {
        this.projectUserList = data;
    }
}

decorate(ProjectStore, {
    // state
    projectList: observable,
    projectTypes: observable,
    projectUserList: observable,
    editingProjectData: observable,

    //actions
    setInitialState: action,
    onGetProjectList: action.bound,
    onGetProjectTypes: action,
    setEditingProjectData: action,
    onCreateProject: action,
    onUpdateProject: action,
    onDeleteProject: action,
});

const projectStore = new ProjectStore();

export default projectStore;
