// Core
import { observable, action, decorate } from "mobx";
import logService from "../services/api/LogService";
import get from "lodash.get";
class LogStore {
    logs = [];

    onGetLogs = async params => {
        try {
            const response = await logService.getLogs(params);
            this.logs = get(response, "data.data");
        } catch (err) {
            console.error(err);
        }
    };

    onAddLog = async params => {
        try {
            await logService.addLog(params);
        } catch (err) {
            console.error(err);
        }
    };

    onUpdateLog = async (params, pathId) => {
        try {
            await logService.updateLog(params, pathId);
        } catch (err) {
            console.error(err);
        }
    };

    onDeleteLog = async id => {
        try {
            await logService.deleteLog(id);
            this.onGetLogs();
        } catch (err) {
            console.error(err);
        }
    };

    set logs(logs) {
        this.logs = logs;
    }
}

decorate(LogStore, {
    // state
    logs: observable.ref,

    // actions
    onGetLogs: action,
    onAddLog: action,
    onUpdateLog: action,
    onDeleteLog: action,
});

const logStore = new LogStore();

export default logStore;
