// Core
import React from "react";

// UI
import { RadioGroup, FormControlLabel, Radio, Grid } from "@material-ui/core";

function TypeSelector({ value, projectTypes, onChange }) {
    return (
        <RadioGroup
            aria-label="Type"
            name="type"
            value={value}
            onChange={onChange}
        >
            <Grid container wrap="nowrap">
                {projectTypes.map(item => (
                    <FormControlLabel
                        key={`project-type-${item}`}
                        value={item}
                        control={<Radio color={"primary"} />}
                        label={item}
                    />
                ))}
            </Grid>
        </RadioGroup>
    );
}

export default TypeSelector;
