// Core
import React from "react";
import moment from "moment";
import MomentUtils from "@date-io/moment";
// UI
import {
    KeyboardTimePicker,
    MuiPickersUtilsProvider,
} from "@material-ui/pickers";
// Constants
import { TIME_FORMAT } from "constants/dateFormats";

export default function Developers({ value, id, label, onChange, error }) {
    const handleChange = momentTime => {
        onChange(id, momentTime);
    };

    return (
        <>
            <MuiPickersUtilsProvider utils={MomentUtils}>
                <KeyboardTimePicker
                    ampm={false}
                    id={id}
                    error={!!error}
                    label={label}
                    value={moment(value, TIME_FORMAT)}
                    onChange={handleChange}
                    helperText={error}
                />
            </MuiPickersUtilsProvider>
        </>
    );
}
