const ROUTES = Object.freeze([
    {
        name: "Reports",
        path: "developer-reports",
    },
    {
        name: "Reports",
        path: "manager-reports",
    },
    {
        name: "Projects",
        path: "projects",
    },
    {
        name: "Developers",
        path: "developers",
    },

    {
        name: "Report history",
        path: "report-history",
    },
]);

const ROUTES_FOR_DEV = Object.freeze(["developer-reports"]);
const ROUTES_FOR_ADMIN = Object.freeze([
    "report-history",
    "developers",
    "projects",
    "manager-reports",
]);

const BY_ROLE = Object.freeze({
    admin: ROUTES_FOR_ADMIN,
    dev: ROUTES_FOR_DEV,
});

export const getRoutesByRole = role => {
    const routesByRole = BY_ROLE[role];

    if (!routesByRole) return [];

    return ROUTES.filter(({ path }) => routesByRole.includes(path));
};
