// Core
import React, { useEffect, useState } from "react";
import { useObserver } from "mobx-react";
import moment from "moment";
// UI
import { Container, Grid, Box } from "@material-ui/core";
import AddButton from "components/AddButton";
import WeekSelector from "components/WeekSelector";
import WeekDates from "components/WeekDates";
import ReportTable from "Pages/developer-reports/ReportTable";
import ReportForm from "Pages/developer-reports/ReportForm";
import NoteTitle from "./NoteTitle";
// Utils
import { useStores } from "../../store";

// Utils
import { getParametersForLogs, getTotalTime } from "utils/developerReportUtils";

function useStore() {
    const store = useStores();

    return useObserver(() => ({
        logs: store.logStore.logs,
        logTypes: store.logTypesStore.logTypes,
        projectList: store.projectStore.projectList,
        onGetLogs: store.logStore.onGetLogs,
        onGetLogTypes: store.logTypesStore.onGetLogTypes,
        onGetProjectList: store.projectStore.onGetProjectList,
        onAddLog: store.logStore.onAddLog,
        onUpdateLog: store.logStore.onUpdateLog,
        onDeleteLog: store.logStore.onDeleteLog,
    }));
}

export default function Developers() {
    const {
        logs,
        logTypes,
        projectList,
        onGetLogs,
        onGetProjectList,
        onGetLogTypes,
        onAddLog,
        onUpdateLog,
        onDeleteLog,
    } = useStore();

    const [selectedWeek, setWeek] = useState(moment().week());
    const [selectedDay, setDay] = useState();
    const [isModalVisible, setModalVisibility] = useState(false);
    const [editingLog, setEditingLog] = useState();

    useEffect(() => {
        setDay(undefined);
    }, [selectedWeek]);

    useEffect(() => {
        if (selectedWeek) {
            onGetLogs(getParametersForLogs(selectedWeek));
        }
    }, [onGetLogs, selectedWeek]);

    useEffect(() => {
        onGetProjectList();
        onGetLogTypes({
            params: {
                page: 1,
            },
        });
    }, [onGetLogTypes, onGetProjectList]);

    const showModal = () => {
        setModalVisibility(true);
    };

    const closeModal = () => {
        setModalVisibility(false);
        setEditingLog(null);
    };

    const handleEditLog = log => {
        setEditingLog(log);
        showModal();
    };

    const handleSubmitForm = async (data, id) => {
        if (editingLog) {
            await onUpdateLog(data, id);
            await onGetLogs(getParametersForLogs(selectedWeek));
            return;
        }
        await onAddLog(data);
        await onGetLogs(getParametersForLogs(selectedWeek));
    };

    const handleDeleteLog = async id => {
        await onDeleteLog(id);
        await onGetLogs(getParametersForLogs(selectedWeek));
    };

    return (
        <Container>
            <Grid container wrap="wrap">
                <Box width="10%" m={2} minWidth={120}>
                    <WeekSelector
                        setWeek={setWeek}
                        selectedWeek={selectedWeek}
                    />
                </Box>
                <Box m={2}>
                    <WeekDates
                        selectedWeek={selectedWeek}
                        selectedDay={selectedDay}
                        setDay={setDay}
                        logs={logs}
                    />
                </Box>
                <Box flexGrow={1} m={2}>
                    <Grid container justify="flex-end">
                        Total work time: {getTotalTime(logs)}
                    </Grid>
                </Box>
                <Grid item xs={12}>
                    <NoteTitle>Here you can log your working time</NoteTitle>
                </Grid>
                <ReportTable
                    data={logs}
                    projects={projectList}
                    logTypes={logTypes}
                    handleEditLog={handleEditLog}
                    selectedDay={selectedDay}
                    selectedWeek={selectedWeek}
                />
                <Grid item>
                    <AddButton handleClick={showModal}>Add</AddButton>
                </Grid>
                {isModalVisible && (
                    <ReportForm
                        open={isModalVisible}
                        handleClose={closeModal}
                        projects={projectList}
                        logTypes={logTypes}
                        submitForm={handleSubmitForm}
                        deleteLog={handleDeleteLog}
                        initialData={editingLog}
                        selectedDay={selectedDay}
                    />
                )}
            </Grid>
        </Container>
    );
}
