// Core
import React from "react";

// UI
import {
    TableContainer,
    TableHead,
    TableCell,
    TableBody,
    Paper,
    Table,
} from "@material-ui/core";
import TableRow from "components/TableRow";

// Constants
import { TABLE_HEADERS } from "constants/projects";

export default function ({ projectList, setEditingProjectData }) {
    return (
        <TableContainer component={Paper}>
            <Table aria-label="simple table">
                <TableHead>
                    <TableRow>
                        {TABLE_HEADERS.map(item => (
                            <TableCell key={item}>{item}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {projectList.length ? (
                        projectList.map(project => (
                            <TableRow
                                hover
                                key={project.id}
                                onClick={() => setEditingProjectData(project)}
                            >
                                <TableCell component="th" scope="row">
                                    {project.id}
                                </TableCell>
                                <TableCell>{project.num_of_dev}</TableCell>
                                <TableCell>{project.name}</TableCell>
                                <TableCell>{project.type}</TableCell>
                                <TableCell>{project.total_log}</TableCell>
                            </TableRow>
                        ))
                    ) : (
                        <TableRow>
                            <TableCell>{"Empty"}</TableCell>
                        </TableRow>
                    )}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
