// Core
import React, { useState, useEffect } from "react";
import { useObserver } from "mobx-react";
// UI
import { Grid, Container, Typography } from "@material-ui/core";
import ProjectsTable from "./ProjectsTable";
import CreateProjectForm from "./CreateProjectForm";
import AddButton from "components/AddButton";

// Utils
import { useStores } from "../../store";

// Styled
import { StyledGrid } from "./styles";

function useStore() {
    const { userStore, projectStore } = useStores();

    return useObserver(() => ({
        projectList: projectStore.projectList,
        projectTypes: projectStore.projectTypes,
        userList: userStore.userList,
        onGetProjectList: projectStore.onGetProjectList,
        onGetUserList: userStore.onGetUserList,
        onCreateProject: projectStore.onCreateProject,
        onUpdateProject: projectStore.onUpdateProject,
        onDeleteProject: projectStore.onDeleteProject,
        onGetProjectTypes: projectStore.onGetProjectTypes,
        setEditingProjectData: projectStore.setEditingProjectData,
        editingProjectData: projectStore.editingProjectData,
    }));
}

export default function Projects() {
    const [isModalVisible, setModalVisibility] = useState(false);

    const {
        projectList,
        projectTypes,
        userList,
        onCreateProject,
        onUpdateProject,
        onGetProjectList,
        onGetProjectTypes,
        onGetUserList,
        setEditingProjectData,
        editingProjectData,
        onDeleteProject,
    } = useStore();

    useEffect(() => {
        onGetProjectList();
        onGetProjectTypes();
        onGetUserList();
    }, [onGetProjectList, onGetProjectTypes, onGetUserList]);

    useEffect(() => {
        if (editingProjectData) {
            showModal();
        }
    }, [editingProjectData]);

    const showModal = () => {
        setModalVisibility(true);
    };

    const closeModal = () => {
        setModalVisibility(false);
        setEditingProjectData();
    };

    const handleDeleteProject = id => {
        onDeleteProject(id);
        closeModal();
    };

    return (
        <Container>
            <Grid container>
                <StyledGrid item xs={12}>
                    <Typography variant="h6">Available projects:</Typography>
                </StyledGrid>
                <Grid item xs={12}>
                    <ProjectsTable
                        projectList={projectList}
                        setEditingProjectData={setEditingProjectData}
                    />
                </Grid>
            </Grid>
            <Grid item xs>
                <AddButton handleClick={showModal}>Create project</AddButton>
            </Grid>
            {isModalVisible && (
                <CreateProjectForm
                    open={isModalVisible}
                    onClose={closeModal}
                    userList={userList}
                    projectTypes={projectTypes}
                    handleSubmit={
                        editingProjectData ? onUpdateProject : onCreateProject
                    }
                    initialData={editingProjectData}
                    handleDeleteProject={handleDeleteProject}
                />
            )}
        </Container>
    );
}
