// Core
import React from "react";
import { useFormik } from "formik";
// UI
import { Grid, TextField } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import FormDialog from "components/FormDialog";
import SkillMultiSelect from "../SkillsSelector";
import RateInput from "../RateInput";
// Styles
import { StyledGridContainer } from "./styles";
// Utils
import { getInitialFormValues } from "utils/developersUtils";
import RoleSelector from "../RoleSelector";

export default function CreateUserForm({
    open,
    onClose,
    handleSubmit,
    initialData,
    handleDeleteUser,
}) {
    const initialValues = getInitialFormValues(initialData);

    const formik = useFormik({
        initialValues,
        onSubmit: async data => {
            await handleSubmit(data, initialData?.id);
            onClose();
        },
    });

    return (
        <FormDialog open={open} handleClose={onClose} title="Project Item:">
            <form autoComplete="off">
                <StyledGridContainer>
                    <Grid container>
                        <Grid item xs>
                            <TextField
                                id="firstname"
                                label="First name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.firstname}
                                autoComplete="off"
                            />
                        </Grid>
                        <Grid item xs>
                            <TextField
                                id="lastname"
                                label="Last name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.lastname}
                                autoComplete="off"
                            />
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <SkillMultiSelect
                            values={formik.values.skills}
                            onChange={formik.setFieldValue}
                            id="skills"
                        />
                    </Grid>
                    <Grid container justify={"space-between"}>
                        <Grid item xs={8}>
                            <TextField
                                id="country"
                                label="Country"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.country}
                            />
                        </Grid>
                        <Grid item xs={2}>
                            <RateInput
                                onChange={formik.setFieldValue}
                                id="rate"
                                value={formik.values.rate}
                            />
                        </Grid>
                    </Grid>
                    <Grid item xs>
                        <TextField
                            id="email"
                            label="e-mail"
                            onChange={formik.handleChange}
                            defaultValue={formik.values.email}
                        />
                    </Grid>
                    <Grid container justify="flex-end">
                        <RoleSelector
                            value={formik.values.role}
                            onChange={formik.setFieldValue}
                            id="role"
                        />
                    </Grid>
                    <DialogActions>
                        {initialData && (
                            <Button
                                onClick={() =>
                                    handleDeleteUser(initialData?.id)
                                }
                                color="secondary"
                            >
                                Delete
                            </Button>
                        )}
                        <Button onClick={onClose} color="primary">
                            Cancel
                        </Button>
                        <Button onClick={formik.handleSubmit} color="primary">
                            Save
                        </Button>
                    </DialogActions>
                </StyledGridContainer>
            </form>
        </FormDialog>
    );
}
