// Core
import React from "react";
import { useFormik } from "formik";

// UI
import { TextField, Grid } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import FormDialog from "components/FormDialog";
import UserMultiSelect from "../UserMultiSelect";
import TypeSelector from "../TypeSelector";

// Styles
import { StyledGridContainer } from "./styles";

// Utils
import { getInitialFormValues } from "utils/projectUtils";

// Constants

export default function LogForm({
    open,
    onClose,
    userList,
    projectTypes,
    handleSubmit,
    initialData,
    handleDeleteProject,
}) {
    const initialValues = getInitialFormValues(initialData);

    const formik = useFormik({
        initialValues,
        onSubmit: async data => {
            await handleSubmit(data, initialData?.id);
            onClose();
        },
    });

    return (
        <FormDialog open={open} handleClose={onClose} title="Project Item:">
            <StyledGridContainer>
                <Grid container>
                    <Grid item>
                        <TextField
                            required
                            id="name"
                            label="Project name"
                            onChange={formik.handleChange}
                            defaultValue={formik.values.name}
                        />
                    </Grid>
                    <Grid item>
                        <TextField
                            required
                            id="alias"
                            label="Alias"
                            onChange={formik.handleChange}
                            defaultValue={formik.values.name}
                        />
                    </Grid>
                </Grid>
                <Grid item xs>
                    <TextField
                        id="descriptions"
                        defaultValue={formik.values.descriptions}
                        onChange={formik.handleChange}
                        label="Description"
                        multiline
                        fullWidth
                        rows={4}
                        variant="outlined"
                    />
                </Grid>
                <Grid item xs={12}>
                    <UserMultiSelect
                        onChange={formik.setFieldValue}
                        values={formik.values.users}
                        userList={userList}
                        id={"users"}
                    />
                </Grid>
                <Grid container justify="flex-end">
                    <TypeSelector
                        value={formik.values.type}
                        projectTypes={projectTypes}
                        onChange={formik.handleChange}
                    />
                </Grid>
                <DialogActions>
                    {initialData && (
                        <Button
                            onClick={() => handleDeleteProject(initialData?.id)}
                            color="secondary"
                        >
                            Delete
                        </Button>
                    )}
                    <Button onClick={onClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={formik.handleSubmit} color="primary">
                        Save
                    </Button>
                </DialogActions>
            </StyledGridContainer>
        </FormDialog>
    );
}
