// Core
import { observable, action, decorate } from "mobx";
import userService from "../services/api/UserService";
import get from "lodash.get";

const fetchStrategy = ({ body, id }) => ({
    delete: () => userService.deleteUser(id),
    update: () => userService.updateUser(body, id),
    create: () => userService.createUser(body),
});
class UserStore {
    userList = [];
    editingUserData = undefined;

    onGetUserList = async () => {
        try {
            const response = await userService.getUserList();
            this.userList = get(response, "data.data");
        } catch (err) {
            console.error(err);
        }
    };

    handleFetchUserData = async (actionType, data) => {
        try {
            await fetchStrategy(data)[actionType]();
            this.onGetUserList();
        } catch (error) {
            console.error(error);
        }
    };

    onCreateUser = body => {
        this.handleFetchUserData("create", { body });
    };

    onUpdateUser = (body, id) => {
        this.handleFetchUserData("update", { body, id });
    };

    onDeleteUser = id => {
        this.handleFetchUserData("delete", { id });
    };

    set userList(userList) {
        this.userList = userList;
    }

    setEditingUserData = user => {
        this.editingUserData = user;
    };
}

decorate(UserStore, {
    // state
    userList: observable,
    editingUserData: observable,

    // actions
    setEditingUserData: action,
    onGetUserList: action,
    onCreateUser: action,
    onUpdateUser: action,
    onDeleteUser: action,
});

const userStore = new UserStore();

export default userStore;
