// Core
import { observable, action, decorate } from "mobx";
import logTypesService from "../services/api/LogTypesService";

class LogTypesStore {
    logTypes = [];

    onGetLogTypes = async config => {
        try {
            const { data } = await logTypesService.getLogTypes(config);
            this.setLogTypes(data);
        } catch (err) {
            console.error(err);
        }
    };

    setLogTypes = ({ data }) => {
        this.logTypes = data;
    };
}

decorate(LogTypesStore, {
    // state
    logTypes: observable.ref,

    // actions
    setLogTypes: action.bound,
    onGetLogTypes: action,
});

const logTypesStore = new LogTypesStore();

export default logTypesStore;
